import React, { useState } from 'react';
import imageCompression from "browser-image-compression";
import './compressor.css';
import Download from '../Images/Download.png';
import Upload from '../Images/Upload.png';
import { Navbar, Card } from "react-bootstrap";

/* Font Awesome Icons */
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faImage } from '@fortawesome/free-solid-svg-icons';


function Compressor() {

  const [compressedLink, setCompressedLink] = useState("");
  const [originalImage, setOriginalImage] = useState("");
  const [originalLink, setOriginalLink] = useState("");
  const [clicked, setClicked] = useState(false);
  const [uploadImage, setUploadImage] = useState(false);
  const [outputFileName, setOutputFileName] = useState("");

  // image upload Handling part
  function uploadLink(event) {
    const imageFile = event.target.files[0];
    setOriginalLink(URL.createObjectURL(imageFile));
    setOriginalImage(imageFile);
    setOutputFileName(imageFile.name);
    setUploadImage(true);
  }

  // Compress image when button is clicked
  function click(e) {
    e.preventDefault();
    const options = {
      maxSizeMB: 3,
      maxWidthOrHeight: 800,
      useWebWorker: true
    };

    if (options.maxSizeMB >= originalImage.size / 1024) {
      alert("Please upload a larger image.");
      return;
    }

    // Compress the image
    imageCompression(originalImage, options).then(x => {
      const downloadLink = URL.createObjectURL(x);
      setCompressedLink(downloadLink);
    });

    setClicked(true);
  }

  return (
    <div className="mainContainer">
      <Navbar className="navbar justify-content-center " bg="light" variant="light">
        <Navbar.Brand className="navbar-content" href="/">
          <FontAwesomeIcon className="social-icons" icon={faImage} />{' '}
          Image Compressor
        </Navbar.Brand>
      </Navbar>

      <div className="row mt-5">
        {/* Column for Upload */}
        <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 text-center">
          {uploadImage ? (
            <Card.Img
              className="image-preview"
              variant="top"
              src={originalLink}
              alt="Original Image"
            />
          ) : (
            <Card.Img
              className="uploadCard image-placeholder"
              variant="top"
              src={Upload}
              alt="Upload Placeholder"
            />
          )}
          <div className="d-flex justify-content-center upload-btn-wrapper upload-btn">
            <button className="btn btn-dark">Upload a file</button>
            <br></br>
            <input
              type="file"
              accept="image/*"
              className="mt-2 btn btn-dark w-75"
              onChange={uploadLink}
            />
          </div>
        </div>

        {/* Column for Compress Button */}
        
        <div className="col-xl-4 col-lg-4 col-md-12 mb-5 mt-4 col-sm-12 d-flex justify-content-center align-items-center">
          {outputFileName && (
            <button type="button" className="btn btn-dark" onClick={click}>
              Compress
            </button>
          )}
        </div>

        {/* Column for Download */}
        <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 text-center mt-3 final">
          {clicked ? (
            <div>
              <Card.Img className="image-preview" variant="top" src={compressedLink} alt="Compressed Image" />
              <div className="d-flex justify-content-center">
                <a
                  href={compressedLink}
                  download={outputFileName}
                  className="download-btn mt-2 w-75"
                >
                  Download
                </a>
              </div>
            </div>
          ) : (
            <Card.Img
              className="uploadCard image-placeholder"
              variant="top"
              src={Download}
              alt="Download Placeholder"
            />
          )}
        </div>
      </div>
    </div>
  );
}

export default Compressor;
